@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@1,700&family=Poppins:wght@300;600&display=swap);

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn-about {
    border-radius: 1px;
    font-family: Arial;
    color: #C40D2E;
    font-size: 15px;
    font-weight: bold;
    background: #ffff;
    padding: 8px 25px 8px 25px;
    border: solid #C40D2E 2px;
    border-radius: 5px;
    transition: .5s;
    text-decoration: none;
  }
  
  .btn-about:hover {
    background: #C40D2E;
    color: white;

    text-decoration: none;
  }
  .img-up-down{
  
   position: absolute;
  
  }
  .img-up-down {
    -webkit-animation: MoveUpDown 2s linear infinite;
            animation: MoveUpDown 2s linear infinite;
    position: relative;
    left: 0;
    bottom: 0;
  }
  .img-up-down img{
    border-radius: 1rem;
  }
  @-webkit-keyframes MoveUpDown {
    0%, 100% {
      top: 0;
    }
    50% {
      top: 1.5rem;
    }
  }
  @keyframes MoveUpDown {
    0%, 100% {
      top: 0;
    }
    50% {
      top: 1.5rem;
    }
  }
