.img-hover-zoom {
  position: relative;
  margin: auto;
  overflow: hidden;
  }
  
  /* [2] Transition property for smooth transformation of images */
  .img-hover-zoom img {
    max-width: 100%;
    transition: all 0.3s;
    display: block;
    width: 100%;
    height: auto;
    
    -webkit-transform: scale(1);
    
            transform: scale(1);
  }
  
  /* [3] Finally, transforming the image when container gets hovered */
  .img-hover-zoom:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

